body {
    background-image: url("image/background.jpg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover; 
    background-color: #cccccc;
   }

.app {

    padding: 30px;
    height: 100vh;
  }
  
  .todo-list {
    background: #e8e8e8;
    border-radius: 10px;
    padding: 5px;
    max-width: 800px;
    margin: auto;
  }
  
  .todo {
    background: #fff;
    box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.15);
    padding: 3px 10px;
    font-size: 12px;
    /* margin-bottom: 6px; */
    border-radius: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  